@import 'assets/scss/utils/mixins';

.close-btn {
	width: 40px;
	height: 40px;
	background: var(--general-1);
	border: none;
	border-radius: 50%;
}

.py-10px {
	padding-top: 10px;
	padding-bottom: 10px;
}

.view-date {
	background-color: rgba(255, 90, 184, 0.08);
	border: 1px solid var(--prim-2);
	border-radius: 4px;
}

.tenant-img {
	border-radius: 50%;
	object-fit: cover;
}

.on-hunt-badge {
	position: absolute;
	bottom: 0;
}

.avatar {
	width: 100px;
	height: 100px;
	border: 5px solid var(--basic-1);
	border-radius: 50%;
}
